.navbar {
  position: fixed;
  top: 0;
  z-index: 100;
  height: 100%;
  width: 6rem;
  background-color: #111;
  border-right: 1px solid #333;
  transition: all, 0.2s;

  &-container {
    display: flex;
    flex-direction: column;
    margin-top: 1rem;
    overflow: hidden;
    user-select: none;
  }

  ul {
    list-style-type: none;
    padding: 0;
    width: 20rem;
  }

  li {
    margin-bottom: 1rem;
    display: flex;
    flex-direction: row;
    cursor: pointer;
  }

  &:hover {
    width: 20rem;
    transition: all, 0.2s;
  }
}
